import React from "react";
import styled from 'styled-components';
import classNames from "classnames";
import styles from './main.module.css';

const Main_img = styled.div`
    width: 100%;
    height: 300px;
    background: yellow;
`;

const MainCom = () => {
    return (
        <div>
            <Main_img/>
            <div className={styles.contents}>
                <div className={classNames(styles.con1, styles.con)}></div>
                <div className={classNames(styles.con2, styles.con)}></div>
                <div className={classNames(styles.con3, styles.con)}></div>
                <div className={classNames(styles.con4, styles.con)}></div>
            </div>
            {/* .contents */}
        </div>
    );
}

export default MainCom;