import React from "react";
import styledComponents from "styled-components";
import BestItem from "./best_item";

const BestBlock = styledComponents.div`
    width: 1500px;
    padding: 50px 0;
    margin: 0 auto;

    @media (max-width: 1500px) {
        width: 90%;
    }
`;

const Best = () => {
    return (
        <div>
            <BestBlock>
                <BestItem/>
                <BestItem/>
                <BestItem/>
                <BestItem/>
                <BestItem/>
                <BestItem/>
                <BestItem/>
                <BestItem/>
                <BestItem/>
                <BestItem/>
            </BestBlock>
        </div>
    );
}

export default Best;